* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.applicants,
.dates {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.navbar {
    background-color: black;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;

  }
  
  .menu-user {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    position:relative;
    left:73%;
   
  }
  
  .admin-toggle{ 
      font-size: 20px;
      color:#fff;
    text-decoration: none;
margin-left: 2rem;
margin-top: 2rem;}


  .menu-user a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
  }
  .nav-menu {
    background-color: black;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right:-82%;
    transition: 850ms;
    z-index: 100;
  }
  
  .nav-menu.active {
    right: 0%;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
   
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color:  black;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
